@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@media screen and (max-device-width: 480px) {
    body {
        -webkit-text-size-adjust: none;
    }
}

img.picto {
    width: 24px;
    shape-rendering: geometricPrecision;
}

.horizontal-divider {
    width: 100%;
    height: 3px;
}

.chrome {
    box-shadow: 0 0px 5px 2px rgba(0, 0, 0, 0.12);
    position: absolute;
    z-index: 10;
    background: white;
    border-radius: 1px;
    text-align: center;
    line-height: 20px;
}

.chrome a {
    text-decoration: none;
    color: rgb(102, 102, 102);
}

.chrome a:hover {
    color: black;
}

.chrome-button {
    cursor: pointer;
}

.chrome-button:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.zoom {
    top: 20px;
    right: 20px;
    width: 40px;
    line-height: 1px;
}

.zoom .chrome-button {
    height: 40px;
    font-size: 30px;
    line-height: 34px;
    padding: 0;
}

.zoom .chrome-button .picto {
    margin: 8px;
}

.zoom .chrome-button.zoom-shuffle {
    font-size: 20px;
    line-height: 40px;
    border-bottom: none;
}

.arrow {
    bottom: 20px;
    right: 20px;
    flex: 1 1;
    flex-direction: row;
}

.arrow .arrow-left,
.arrow .arrow-right {
    display: inline-block;
    width: 70px;
    height: 70px;
}

.arrow .arrow-left img.picto,
.arrow .arrow-right img.picto {
    width: 50px;
    margin: 8px;
    margin-top: 9px;
    margin-bottom: 7px;
}

.arrow-left {
    border-right: 1px solid rgb(102, 102, 102);
}

.shuffle {
    width: 100px;
    height: 40px;
    bottom: 50vh;
    left: 20px;
}

.chrome-button.chrome-button-search {
    display: inline-block;
    padding-left: 7px;
    padding-right: 7px;
    padding-top: 5px;
}

.chrome.menu {
    left: 20px;
    top: 10px;
    line-height: 1px;
}


.menu .menu-section {
    padding: 5px 10px 5px;
    text-align: left;
    font-size: 18px;
    line-height: 24px;
}

.menu-filters-option {
    cursor: pointer;
    padding-left: 20px;
}

.menu-filters-option img.picto {
    width: 16px;
}

.menu .close-button {
    cursor: pointer;
    position: absolute;
    right: 5px;
    bottom: 3px;
}

.menu .close-button img.picto {
    width: 22px;
}

/* montserrat-300 - latin */

@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
}

* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: "Montserrat", -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
    color: rgb(102, 102, 102);
    position: fixed;
    overscroll-behavior-y: none;
}

#root {
    position: absolute;
    width: 100%;
    height: 100%;
    display: block;
}

#container {
    height: 100%;
    touch-action: none;
}

.bar {
    position: absolute;
    background-color: black;
    z-index: 10;
}

.vertical.bar {
    left: 50%;
    height: 100%;
    width: 1px;
}

.horizontal.bar {
    top: 50%;
    width: 100%;
    height: 1px;
}

#map {
    height: 100%;
    pointer-events: none;
}

.image {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
}

.image-container {
    position: absolute;
    will-change: transform;
    pointer-events: none;
}

.image-container .image {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    will-change: width, height;
    cursor: pointer;
    pointer-events: auto;
}

.image-container .legend {
    margin-top: 12px;
    text-rendering: optimizeSpeed;
    font-size: 14px;
    /*text-transform: uppercase;*/
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.debug-info {
    position: absolute;
    color: white;
    z-index: 1000;
    width: 300px;
    left: 300px;
    top: 200px;
    opacity: 0.6;
    display: flex;
    flex-wrap: wrap;
}

